html , body{
  min-height: 100% ;
}


ul,
ol {
	list-style: none;
  margin: 0;
  padding: 0;
}


body {
  font-family: 'Rubik', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen','Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;

  margin: 0;
	padding: 0;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

background-color: lightyellow;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.App{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-height: 100% ;

  display: flex;
  flex-direction: column;
}

main{
  margin: 30px 0 ;
  padding: 0 15px;
  text-align: center;
}

h1 {
  color: #333;
  word-spacing: 8px;
}

.btn {

  font-size: 16px;
    background-color: inherit;
    outline: none;
    border: 0;
     border: 2px solid #555;
			padding: 3px 10px;
			border-radius: 5px;
			font-size: 30px;
			color: #222;
      cursor: pointer;

      &:hover, &:focus{
        opacity: .7;
        color: #666;
      }
}
