.nav-list {
	display: flex;
	align-items: center;

	flex-direction: column;
	gap: 20px;

	@media (min-width: 992px) {
		flex-direction: row;
		gap: 25px;
		margin-left: auto;
	}
	&__item {
		&__Link {
			&:link,
			&:visited {
				font-size: large;
				font-weight: bold;
				color: #888;
				text-decoration: none;
				text-transform: uppercase;
				transition: all 0.3s;
				border-bottom: 2px solid transparent;
			}
			&:hover,
			&:active,
			&.active {
				color: #333;
				border-bottom: 2px solid #333;
			}
		}
	}
}
