.social-list {
	padding: 0;
	margin: 0;
	display: flex;
	align-items: center;
	gap: 1rem;

	@media (min-width: 992px) {
		margin-left: auto;
	}

	&__item {
		&__link {
			&:link,
			&:visited {
				color: #888;
				transition: all 0.2s;
				display: block;
				border-radius: 50%;
				width: 2rem;
				height: 2rem;
				border: 2px solid #888;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			&:hover,
			&:active {
				color: #333;
				border: 2px solid #333;
				transform: rotate(1turn);
			}
		}
	}
}
