.header {
	padding: 10px 20px;
  background-color: lightgoldenrodyellow;
  box-shadow: 0 0 2px 2px #999;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;


	&__logo {
    margin: 0 ;

		&__link {
      display: block;

      &:link, &:visited{
		border: 2px solid #555;
			padding: 3px 10px;
			border-radius: 5px;
			font-size: 30px;
			color: #222;
			font-style: italic;
			text-decoration: none;
      }

      &:hover, &:active , &:focus{
        opacity: .7;
        color: inherit;
      }

		}
	}

	&__toggler-button {
		border: none;
		padding: 5px 7px;
		border: 1.5px solid #333;
		background-color: transparent;
		border-radius: 2px;
		cursor: pointer;
		transition: all 0.3s;

		display: flex;
		align-items: center;
		justify-content: center;
		display: block;

		@media (min-width: 992px) {
			display: none;
		}

		&:hover {
			border: 1.5px solid #333;
			transform: translateY(-3px);
		}

		&:active {
			border: 1.5px solid #333;
			transform: translateY(-1px);
		}

		&:active {
			border: 1.5px solid #333;
		}

		&__icon {
			width: 20px;
			height: 20px;
			color: #999;
			transition: all 0.3s;
		}

		&:hover &__icon {
			color: #333;
		}
	}

	&__nav {
    display: flex;
    flex-grow: 1;
		justify-content: space-between;


    @media (max-width: 991px) {
		max-height: 0;
		visibility: hidden;
    overflow: hidden;

    width: 100%;
		transition: all .8s;

    display: flex;
    align-items: center;
    justify-content: center;
		flex-direction: column;
		gap: 10px;

    &--show {
    max-height: 100vh;
		visibility: visible;
    overflow: visible;

    	padding-top: 10px ;
		}

  }


		}

}
